import { Canvas, extend, useFrame } from '@react-three/fiber';
import React, { useRef, useState } from 'react';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { Font } from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { BoxBufferGeometry } from 'three';
import './Scss/High.scss';
import Font1 from './font/Font5.json'
import { Link } from 'react-router-dom';
import {createBrowserHistory} from 'history'
import { OrbitControls, Stars, useTexture } from '@react-three/drei';
import { useTranslation } from 'react-i18next';

const Boat = ({handleClick, handleClicked}) => {

    const [scroll, setScroll] = useState(800)
    const width = window.innerWidth
    const height = window.innerHeight
    const camera = useRef(null)
    const boat = useRef(null)
    const car = useRef(null)
    const plane = useRef(null)
    const text = useRef(null)

    const [boatArgs, setBoatArgs] = useState([5, 2, 1.5])
    const [boatP, setBoatP] = useState([-7, 0, 0])
    const [carArgs, setCarArgs] = useState([5, 1.2, 0.8])
    const [carP, setCarP] = useState([-12, -3.6, 0])
    const [planeArgs, setPlaneArgs] = useState([45, 1.5])
    const [planeP, setPlaneP] = useState([-34, -4.35, -0.8])
    const [textArgs, setTextArgs] = useState(0.3)
    const [textP, setTextP] = useState([-6, -4, 0])

    extend({TextGeometry})
    const font = new FontLoader().parse(Font1)
    const options = {font, size: textArgs, height: 0.01, curveSegment: 32, bevelEnabled: true, bevelThickness: 0.03, bevelSize: 0.02, bevelOffset: 0, bevelSegment: 7}
    const [colorText, setColorText] = useState("#021A38")

    const [clicked, click] = useState(false)

    if(clicked){
        document.body.style.cursor = "pointer"
    }else{
        document.body.style.cursor = "auto"
    }

    // const [handleClick, setHandleClick] = useState(false)

    useFrame(() => {
        if(window.scrollY >= scroll){
            boat.current.position.x += 0.02
            boat.current.position.z += 0.008
        }
        if(clicked && car.current.position.x <= 5){
            car.current.position.x += 0.2
            if(plane.current.position.x <= 0){
                plane.current.position.x += 0.2
            }
        }else if(!clicked && car.current.position.x >= -12){
            car.current.position.x -= 0.2
            if(plane.current.position.x >= -34){
                plane.current.position.x -= 0.2
            }
        }

        if(car.current.position.x >= -2){
            setColorText("white")
        }else{
            setColorText("#021A38")
        }

        if(window.scrollX <= 1200){
            text.current.scale.x = 0.8
            text.current.scale.y = 0.8
            text.current.scale.z = 0.8
        }
        if(window.scrollX <= 850){
            text.current.scale.x = 0.95
            text.current.scale.y = 0.95
            text.current.scale.z = 0.95
        }

        if(window.scrollX <= 767){
            // setBoatArgs([4.6, 1.8, 1.2])
            // setBoatP([-6, 0, 0])

            // setCarArgs([4.6, 1.3, 0.5])
            // setCarP([-9, -3.8, 0])

            // setPlaneArgs([45, 1.8])
            // setPlaneP([-30, -4.35, -0.8])

            // setTextP([-4, -4, 0])

            setScroll(500)
            text.current.position.x = -4
            text.current.scale.x = 0.8
            text.current.scale.y = 0.8
            text.current.scale.z = 0.8
        }
        if(window.scrollX <= 650){ 
            text.current.position.x = -3.7
            text.current.scale.x = 0.68
            text.current.scale.y = 0.68
            text.current.scale.z = 0.68
        }

        // else if(window.scrollX <= 500){
            // setBoatArgs([4.6, 1.8, 1.2])
        //     setBoatP([-5, 0, 0])

        //     setCarArgs([4.6, 1.3, 0.5])
        //     setCarP([-7.5, -3.8, 0])

        //     setPlaneArgs([45, 1.8])
        //     setPlaneP([-30, -4.35, -0.8])

        //     setTextP([-4.5, -4, 0])
        //     setTextArgs(0.25)

        //     setScroll(400)
        // }
        // else if(window.scrollX <= 400){
        //     setBoatArgs([4.6, 1.8, 1.2])
        //     setBoatP([-4, 0, 0])

        //     setCarArgs([4.6, 1.3, 0.5])
        //     setCarP([-6.2, -3.8, 0])

        //     setPlaneArgs([45, 1.8])
        //     setPlaneP([-30, -4.35, -0.8])

        //     setTextP([-3.8, -4, 0])
        //     setTextArgs(0.25)

        //     setScroll(400)
        // }else if(window.scrollX <= 370){
        //     setBoatArgs([4.6, 1.8, 1.2])
        //     setBoatP([-4, 0, 0])

        //     setCarArgs([4.6, 1.3, 0.5])
        //     setCarP([-6.2, -3.8, 0])

        //     setPlaneArgs([45, 1.8])
        //     setPlaneP([-30, -4.35, -0.8])

        //     setTextP([-3.6, -4, 0])
        //     setTextArgs(0.25)
        // }



        // if(handleClick){
        //     camera.current.rotation.y = 1.57
        //     camera.current.position.y = 3
        //     setPlaneArgs([0, 0, 0])
        //     setCarArgs([0, 0, 0])
        //     setBoatArgs([0, 0, 0])
        //     setTextArgs(0)
        //     setTimeout(() => {
        //         camera.current.position.z += 0.1
        //     }, 1000);
        // }
    })

    const history = createBrowserHistory();
    

    if(handleClick){
        history.push("/land")
        window.location.reload(false)
    }

    const carImg  = useTexture("images/car.png")

    const {t} = useTranslation()


    return(        
        <perspectiveCamera ref={camera} rotation={[0, 0, 0]} fov={60} aspect={width/height} near={1} far={1000} position={[0, 0, -1]}>
            <mesh position={boatP} ref={boat} rotation={[0, -0.3, 0]}>
                <boxBufferGeometry attach='geometry' args={boatArgs} />
                <meshStandardMaterial attach='material' color={"dodgerblue"} />
            </mesh>
            <mesh position={carP} ref={car} rotation={[0, 0, 0]}>
                <boxGeometry attach='geometry' args={carArgs} />
                <meshStandardMaterial attach='material' map={carImg} />
            </mesh>
            <mesh position={planeP} ref={plane} rotation={[0, 0, 0]}>
                <planeBufferGeometry attach='geometry' args={planeArgs} />
                <meshStandardMaterial attach='material' color={"#CC0C0C"} />
            </mesh>
            <mesh onPointerOver={(event) => click(!clicked)} onPointerOut={(event) => click(!clicked)} position={[0, -4.45, -1]} rotation={[0, 0, 0]} onClick={() => handleClicked()}>
                <planeBufferGeometry attach='geometry' args={planeArgs} />
                <meshPhongMaterial attach='material' color="red" opacity={0} transparent />
            </mesh>
            <mesh position={textP} ref={text} rotation={[-0.1, 0, 0]}>
                <textGeometry attach='geometry' args={[`${t(10)}`, options]} />
                <meshStandardMaterial attach='material' color={colorText} />
            </mesh>    
            
        </perspectiveCamera>      
    )
}

const Model = () => {

    const [handleClick, setHandleClick] = useState(false)

    function handleClicked(){
        setHandleClick(true)
    }

    return (
        <>
            <Canvas className={`${handleClick && "onlyCanvas"}`}>
                <ambientLight />
                <pointLight />
                <Boat handleClick={handleClick} handleClicked={handleClicked} />
                {/* {handleClick &&
                    <>
                        <OrbitControls />
                        <Stars /> 
                    </>
                }                */}
            </Canvas>
        </>
    );
};

export default Model;