import React from 'react';
import Header from '../Header/Header';
import High from '../High/High';
import High2 from '../High2/High2';
import Numbers from '../Numbers/Numbers';

const RouterHeader = ({english, uzbek, russian, dataPartners, dataImages}) => {
    return (
        <div className='RouterHeader'>
            <Header dataImages={dataImages} english={english} russian={russian} uzbek={uzbek} dataPartners={dataPartners} /> 
            <High english={english} russian={russian} uzbek={uzbek} />
            <High2  english={english} russian={russian} uzbek={uzbek} />
            <Numbers dataPartners={dataPartners} english={english} russian={russian} uzbek={uzbek} />
        </div>
    );
};

export default RouterHeader;