import { Canvas, extend, useFrame, useLoader } from '@react-three/fiber';
import React, { useEffect, useRef, useState } from 'react';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { Font, Mesh } from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { BoxBufferGeometry } from 'three';
import './Scss/High2.scss';
import Font1 from './font/Font5.json'
import { createBrowserHistory } from 'history';
import { useTranslation } from 'react-i18next';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

const Boat = () => {

    const width = window.innerWidth
    const height = window.innerHeight
    const camera = useRef(null)
    const boat = useRef(null)
    const car = useRef(null)
    const plane = useRef(null)
    const car1 = useRef(null)
    const plane1 = useRef(null)
    const car2 = useRef(null)
    const plane2 = useRef(null)
    const text = useRef(null)
    const text1 = useRef(null)
    const text2 = useRef(null)

    const [carArgs, setCarArgs] = useState([5, 1.5, 0.8])
    const [carP, setCarP] = useState([-12, 7.3, 0])
    const [planeArgs, setPlaneArgs] = useState([45, 1.5])
    const [planeP, setPlaneP] = useState([-34, 8.15, -0.8])

    const [carP1, setCarP1] = useState([-12, 6.4, 0])
    const [planeArgs1, setPlaneArgs1] = useState([45, 2])
    const [planeP1, setPlaneP1] = useState([-34, 6.75, -0.8])

    const [carP2, setCarP2] = useState([-12, 5.08, 0])
    const [planeArgs2, setPlaneArgs2] = useState([45, 2])
    const [planeP2, setPlaneP2] = useState([-34, 5.4, -0.8])

    const [textArgs, setTextArgs] = useState(0.3)
    const [textP, setTextP] = useState([-3, 7.5, 0])
    const [textArgs1, setTextArgs1] = useState(0.3)
    const [textP1, setTextP1] = useState([-1, 6.2, 0])
    const [textArgs2, setTextArgs2] = useState(0.3)
    const [textP2, setTextP2] = useState([1, 4.9, 0])

    extend({TextGeometry})
    const font = new FontLoader().parse(Font1)
    const options = {font, size: textArgs, height: 0.01, curveSegment: 32, bevelEnabled: true, bevelThickness: 0.03, bevelSize: 0.02, bevelOffset: 0, bevelSegment: 7}
    const [colorText, setColorText] = useState("#021A38")
    const [colorText1, setColorText1] = useState("#021A38")
    const [colorText2, setColorText2] = useState("#021A38")

    const [clicked, click] = useState(false)
    const [clicked1, click1] = useState(false)
    const [clicked2, click2] = useState(false)

    if(clicked || clicked1 || clicked2){
        document.body.style.cursor = "pointer"
    }else{
        document.body.style.cursor = "auto"
    }

    useFrame(() => {
        if(clicked && car.current.position.x <= 6){
            car.current.position.x += 0.2            
            if(plane.current.position.x <= 0){
                plane.current.position.x += 0.2
            }
        }else if(!clicked && car.current.position.x >= -12){
            car.current.position.x -= 0.2            
            if(plane.current.position.x >= -34){
                plane.current.position.x -= 0.2
            }
        }

        if(car.current.position.x >= -2){
            setColorText("gold")
        }else{
            setColorText("#021A38")
        }

        if(clicked1 && car1.current.position.x <= 7.5){
            car1.current.position.x += 0.2
            if(plane1.current.position.x <= 0){
                plane1.current.position.x += 0.2
            }
        }else if(!clicked1 && car1.current.position.x >= -12){
            car1.current.position.x -= 0.2
            if(plane1.current.position.x >= -34){
                plane1.current.position.x -= 0.2
            }
        }

        if(car1.current.position.x >= 0){
            setColorText1("white")
        }else{
            setColorText1("#021A38")
        }

        if(clicked2 && car2.current.position.x <= 8.7){
            car2.current.position.x += 0.2            
            if(plane2.current.position.x <= 0){
                plane2.current.position.x += 0.2
            }
        }else if(!clicked2 && car2.current.position.x >= -12){
            car2.current.position.x -= 0.2
            if(plane2.current.position.x >= -34){
                plane2.current.position.x -= 0.2
            }
        }

        if(car2.current.position.x >= 0.5){
            setColorText2("white")
        }else{
            setColorText2("#021A38")
        }

        if(window.scrollX <= 1300){
            // setTextP([-3.5, 7.5, 0])
            // setTextP1([-1.5, 6.2, 0])
            // setTextP2([0.5, 4.9, 0])
            text.current.position.x = -3.5
            text1.current.position.x = -1.5
            text2.current.position.x = 0.5
        }
        if(window.scrollX <= 1200){
            text.current.scale.x = 0.8
            text.current.scale.y = 0.8
            text.current.scale.z = 0.8

            text1.current.scale.x = 0.8
            text1.current.scale.y = 0.8
            text1.current.scale.z = 0.8

            text2.current.scale.x = 0.8
            text2.current.scale.y = 0.8
            text2.current.scale.z = 0.8
            
            // setTextP([-3.5, 7.5, 0])
            // setTextP1([-2, 6.2, 0])
            // setTextP2([-0.7, 4.9, 0])

            text.current.position.x = -3.5
            text1.current.position.x = -2
            text2.current.position.x = -0.7
        }
        if(window.scrollX <= 850){
            // setTextP([-3, 7.5, 0])
            // setTextP1([-1.5, 6.2, 0])
            // setTextP2([0, 4.9, 0])

            text.current.position.x = -3
            text1.current.position.x = -1.5
            text2.current.position.x = 0
        }
        if(window.scrollX <= 650){

            text.current.position.x = -2.8
            text1.current.position.x = -1.5
            text2.current.position.x = -0.2

            text.current.scale.x = 0.7
            text.current.scale.y = 0.7
            text.current.scale.z = 0.7

            text1.current.scale.x = 0.7
            text1.current.scale.y = 0.7
            text1.current.scale.z = 0.7

            text2.current.scale.x = 0.7
            text2.current.scale.y = 0.7
            text2.current.scale.z = 0.7
        }
    })

    const history = createBrowserHistory();

    function pushHistory(item){
        history.push(item)
        window.location.reload(false)
    }

    const gltf = useLoader(
        GLTFLoader,
        process.env.PUBLIC_URL + "models/plane/scene.gltf"
    )

    useEffect(() => {
        gltf.scene.scale.set(0.05, 0.05, 0.05)
        // gltf.scene.position.set(0, -0.0035, 0)
        gltf.scene.traverse((object) => {
            if(object instanceof Mesh){
                object.castShadow = true
                object.receiveShadow = true
                object.material.envMapItensity = 20
            }
        })
    }, [gltf])
    
    const {t} = useTranslation()

    return(
        <perspectiveCamera ref={camera} rotation={[0, 0, 0]} fov={60} aspect={width/height} near={1} far={1000} position={[0, 0, -6]}>
            {/* <mesh position={[-7, 0, 0]} ref={boat} rotation={[0, -0.3, 0]}>
                <boxBufferGeometry attach='geometry' args={[5, 2, 1.5]} />
                <meshStandardMaterial attach='material' color={"dodgerblue"} />
            </mesh> */}
            <mesh position={carP} ref={car} rotation={[0.5, 1.5, 0.6]}>
                <primitive object={gltf.scene} />
            </mesh>
            <mesh position={planeP} ref={plane} rotation={[0, 0, 0]}>
                {/* <planeBufferGeometry attach='geometry' args={[45, 1.5]} />
                <meshStandardMaterial attach='material' color={"grey"} /> */}
            </mesh>
            <mesh position={textP} ref={text} rotation={[-0.1, 0, 0]}>
                <textGeometry attach='geometry' args={[`${t(7)}`, options]} />
                <meshStandardMaterial attach='material' color={colorText} />
            </mesh>
            <mesh onPointerOver={(event) => click(!clicked)} onPointerOut={(event) => click(!clicked)} position={[0, 8.35, -1]} rotation={[0, 0, 0]} onClick={() => pushHistory("/air")}>
                <planeBufferGeometry attach='geometry' args={planeArgs} />
                <meshPhongMaterial attach='material' color="red" opacity={0} transparent />
            </mesh>

            <mesh position={carP1} ref={car1} rotation={[0, 0, 0]}>
                <boxBufferGeometry attach='geometry' args={[5, 1.2, 0.8]} />
                <meshStandardMaterial attach='material' color={"pink"} />
            </mesh>
            <mesh position={planeP1} ref={plane1} rotation={[0, 0, 0]}>
                <planeBufferGeometry attach='geometry' args={[45, 1.5]} />
                <meshStandardMaterial attach='material' color={"hotpink"} />
            </mesh>
            <mesh position={textP1} ref={text1} rotation={[-0.1, 0, 0]}>
                <textGeometry attach='geometry' args={[`${t(8)}`, options]} />
                <meshStandardMaterial attach='material' color={colorText1} />
            </mesh>
            <mesh onPointerOver={(event) => click1(!clicked1)} onPointerOut={(event) => click1(!clicked1)} position={[0, 6.85, -1]} rotation={[0, 0, 0]} onClick={() => pushHistory("/railway")}>
                <planeBufferGeometry attach='geometry' args={planeArgs} />
                <meshPhongMaterial attach='material' color="red" opacity={0} transparent />
            </mesh>

            <mesh position={carP2} ref={car2} rotation={[0, 0, 0]}>
                <boxBufferGeometry attach='geometry' args={[5, 1.2, 0.8]} />
                <meshStandardMaterial attach='material' color={"dodgerblue"} />
            </mesh>
            <mesh position={planeP2} ref={plane2} rotation={[0, 0, 0]}>
                <planeBufferGeometry attach='geometry' args={[45, 1.5]} />
                <meshStandardMaterial attach='material' color={"blue"} />
            </mesh>
            <mesh position={textP2} ref={text2} rotation={[-0.1, 0, 0]}>
                <textGeometry attach='geometry' args={[`${t(9)}`, options]} />
                <meshStandardMaterial attach='material' color={colorText2} />
            </mesh>
            <mesh onPointerOver={(event) => click2(!clicked2)} onPointerOut={(event) => click2(!clicked2)} position={[0, 5.4, -1]} rotation={[0, 0, 0]} onClick={() => pushHistory("/ocean")}>
                <planeBufferGeometry attach='geometry' args={planeArgs} />
                <meshPhongMaterial attach='material' color="red" opacity={0} transparent />
            </mesh>
        </perspectiveCamera>
    )
}

const Model2 = () => {

    return (
        <>
            <Canvas>
                <ambientLight />
                <pointLight />
                <Boat />                
            </Canvas>
        </>
    );
};

export default Model2;