import React, { useEffect, useState } from 'react';
import './Scss/About.scss';
import CountUp, { useCountUp } from 'react-countup';
import ScrollToTop from '../Scrolls/ScrollToTop';
import { useTranslation } from 'react-i18next';


const About = ({datAbout, dataPartners, english, uzbek, russian}) => {

    const [count, setCount] = useState(300)
    const [counter, setCounter] = useState(false)

    useEffect(() => {
        function animate(){
            requestAnimationFrame(animate)
            if(window.scrollY>=800){
                setCounter(true)
            }
        }
        animate()
    }, [])

    const {t} = useTranslation()

    return (
        <div className='About'>
            <ScrollToTop />
            <div className="wrapper">
                <h1 className="title">{t("aboutus")}</h1>
                <div className="col-12 img">            
                    <iframe className='i-frame' src="https://www.youtube.com/embed/PK7c9aOb8NQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
                <div className='row f1' id='statistics'>
                    <div className="col-md-3">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>countries</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count/2} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>wherehouses</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count/3} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count} />+</span>
                        </div>
                    </div>
                </div>
                <div className='f2' id='statistics'>
                    <div className="col-6">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={300} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>countries</p>
                            <span className="num"><CountUp start={0} duration={3} end={150} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>wherehouses</p>
                            <span className="num"><CountUp start={0} duration={3} end={100} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={3} end={300} />+</span>
                        </div>
                    </div>
                </div>
                <div className="col-12 text">
                    <p>{t("aboutuss")}</p>
                    {/* {datAbout.map((data) => (
                        <>
                            {english &&
                                <p>{data.about_en}</p>
                            }
                            {russian &&
                                <p>{data.about_ru}</p>
                            }
                            {uzbek &&
                                <p>{data.about_uz}</p>
                            }
                        </>
                    ))} */}
                    <div className="btns">
                        <button className="btn">More about us</button>
                        <button className="btn media">Our Social Media</button>
                    </div>
                </div>
                <div className="partners" id='partners'>
                    <div className="our col-md-4">
                        <h1>{t("partner")}</h1>
                    </div>
                    <div className="col-md-8 imgs">
                        {dataPartners.map((data) => (
                            <div className="col-md-4">
                                <img src={data.image} alt="" />
                            </div>
                        ))}                        
                    </div>
                </div>
                <div className="col-12 wh-count" id='whcountry'>
                    <h1>{t("country")}</h1>
                    <p>{t("mamlakat")}</p>
                    <div className="btns">
                        <button className="btn">Explore</button>
                        <button className="btn media">Mijozlarimizning raqami</button>
                    </div>
                </div>
                <div className="col-12 wh">                    
                    <p>{t("numbers")}</p>
                    <div className="btns">
                        <button className="btn">Get One</button>
                        <button className="btn media">Social Media</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default About;