import React, { useState } from 'react';
import './Scss/Navbar.scss';
import { Link } from 'react-router-dom';
import Img1 from './images/logo.png'
import Img2 from './images/logo2.png'
import { useTranslation } from 'react-i18next';

const Navbar = ({ white, english, uzbek, russian, change1, change2, change3, langTitle }) => {

    window.addEventListener("scroll", function () {
        let navScroll = document.getElementById('nav');
        if (white) {
            navScroll.classList.toggle("white", window.scrollY > 10);
        } else if (!white) {
            navScroll.classList.toggle("black", window.scrollY > 10);
        }
    });

    const [show, setShow] = useState(false)

    const [language, setLanguage] = useState(false)
        
    function changeHandle1(){        
        change1(true)
        setLanguage(!language)
    }
    function changeHandle2(){
        change2(true)
        setLanguage(!language)
    }
    function changeHandle3(){
        change3(true)
        setLanguage(!language)
    }     

    const {t} = useTranslation()

    return (
        <>
            <div id={`nav`} className={`Navbar`}>
                <nav className={`navbar navbar-expand d-flex justify-content-between align-items-center ${white && "navbar-black"}`}>
                    <div className="navbar-brand portfolio col-2 p-0">
                        <Link className="nav-link col-12 p-0" to="/"><img className={`w-750 ${white && "white-img"}`} src={Img1} /> <img className={`w-750 black-img ${!white && "white-img"}`} src={Img2} /></Link>
                    </div>
                    <ul className={`navbar-nav ${show && "show"}`}>                           
                        <li className='nav-item'>
                            <Link to="/" className='nav-link'>{t(1)}</Link>
                        </li>
                        <li className='nav-item'>
                            <Link to="/about" className="nav-link">{t(2)}</Link>
                        </li>
                        <li className='nav-item'>
                            <a href="/#services" className="nav-link">{t(4)}</a>
                        </li>
                        <li className='nav-item'>
                            <a href="#statistics" className="nav-link">{t(3)}</a>
                        </li>
                        <li className='nav-item'>
                            <a href="#partners" className="nav-link">{t(5)}</a>
                        </li>
                        <li className='nav-item'>
                            <a href="#contacts" className="nav-link">{t(6)}</a>
                        </li>
                        <li className='nav-item tel-nav'>
                            <a href='tel:+998-95-555-55-55' className='nav-link'>+998 95  555 55 55</a>
                        </li>
                        <i className="fa fa-times" onClick={() => setShow(false)}></i>
                    </ul>
                    {/* {english && 
                        <ul className={`navbar-nav ${show && "show"}`}>                           
                            <li className='nav-item'>
                                <a href="#products" className='nav-link'>Home</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#about" className="nav-link">About us</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#partners" className="nav-link">Service</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Numbers</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Reviews</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Contact Us</a>
                            </li>
                            <li className='nav-item tel-nav'>
                                <a href='tel:+998-95-555-55-55' className='nav-link'>+998 95  555 55 55</a>
                            </li>
                            <i className="fa fa-times" onClick={() => setShow(false)}></i>
                        </ul>
                    }
                    {russian && 
                        <ul className={`navbar-nav ${show && "show"}`}>                           
                            <li className='nav-item'>
                                <a href="#products" className='nav-link'>Home</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#about" className="nav-link">About us</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#partners" className="nav-link">Service</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Numbers</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Reviews</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Contact Us</a>
                            </li>
                            <li className='nav-item tel-nav'>
                                <a href='tel:+998-95-555-55-55' className='nav-link'>+998 95  555 55 55</a>
                            </li>
                            <i className="fa fa-times" onClick={() => setShow(false)}></i>
                        </ul>
                    }
                    {uzbek && 
                        <ul className={`navbar-nav ${show && "show"}`}>                           
                            <li className='nav-item'>
                                <a href="#products" className='nav-link'>Home</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#about" className="nav-link">About us</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#partners" className="nav-link">Service</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Numbers</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Reviews</a>
                            </li>
                            <li className='nav-item'>
                                <a href="#contacts" className="nav-link">Contact Us</a>
                            </li>
                            <li className='nav-item tel-nav'>
                                <a href='tel:+998-95-555-55-55' className='nav-link'>+998 95  555 55 55</a>
                            </li>
                            <i className="fa fa-times" onClick={() => setShow(false)}></i>
                        </ul>
                    }                    */}
                    <div className='navbar-brand tel'>
                        <a href='tel:+998-95-555-55-55' className='nav-link'>+998 95  555 55 55</a>
                    </div>
                    <div className="d-flex align-items-center justify-content-between">
                        <div className={`language d-flex align-items-center justify-content-between ${white && "lang-black"}`}>
                            {english && 
                                <div className={`change-language`}>
                                    <input type="checkbox" id="language" checked={language} onChange={() => setLanguage(!language)} />
                                    <label htmlFor='language'>ENG</label>
                                    <ul className={`language-menu ${!language && "d-none"}`}>
                                        <li onClick={() => changeHandle1()}><a><a className='nav-link'>RU</a></a></li>
                                        <li onClick={() => changeHandle2()}><a><a className='nav-link'>ENG</a></a></li>
                                        <li onClick={() => changeHandle3()}><a><a className='nav-link'>UZ</a></a></li>                                                                            
                                    </ul> 
                                </div>
                            }
                            {russian && 
                                <div className={`change-language`}>
                                    <input type="checkbox" id="language" checked={language} onChange={() => setLanguage(!language)} />
                                    <label htmlFor='language'>RU</label>
                                    <ul className={`language-menu ${!language && "d-none"}`}>
                                        <li onClick={() => changeHandle1()}><a><a className='nav-link'>RU</a></a></li>
                                        <li onClick={() => changeHandle2()}><a><a className='nav-link'>ENG</a></a></li>
                                        <li onClick={() => changeHandle3()}><a><a className='nav-link'>UZ</a></a></li>                                                                            
                                    </ul> 
                                </div>
                            }
                            {uzbek && 
                                <div className={`change-language`}>
                                    <input type="checkbox" id="language" checked={language} onChange={() => setLanguage(!language)} />
                                    <label htmlFor='language'>UZ</label>
                                    <ul className={`language-menu ${!language && "d-none"}`}>
                                        <li onClick={() => changeHandle1()}><a><a className='nav-link'>RU</a></a></li>
                                        <li onClick={() => changeHandle2()}><a><a className='nav-link'>ENG</a></a></li>
                                        <li onClick={() => changeHandle3()}><a><a className='nav-link'>UZ</a></a></li>                                                                           
                                    </ul> 
                                </div>
                            }                                                                               
                            <i className={`fa fa-bars`} onClick={() => setShow(true)}></i>
                        </div>                        
                    </div>
                </nav>
            </div>
        </>
    );
};

export default Navbar;