import { Canvas, extend, useFrame, useLoader } from '@react-three/fiber';
import React, { useEffect, useRef, useState } from 'react';
import './Scss/Air.scss';
import { OrbitControls, Stars, useTexture } from '@react-three/drei';
import { DoubleSide, Mesh } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

const AirZone = ({changeShow}) => {

    const width = window.innerWidth
    const height = window.innerHeight
    const camera = useRef(null)
    const plane = useRef(null)
    const planeMesh = useRef(null)

    // plane

    const ground  = useTexture("images/ground1.jpg")
    const sky  = useTexture("images/sky1.png")
    const earth  = useTexture("images/earth4.jpg")  

    // car model gltf

    const gltf = useLoader(
        GLTFLoader,
        process.env.PUBLIC_URL + "models/plane/scene.gltf"
    )

    useEffect(() => {
        gltf.scene.scale.set(1, 1, 1)
        gltf.scene.position.set(0, 0, 0)
        gltf.scene.traverse((object) => {
            if(object instanceof Mesh){
                object.castShadow = true
                object.receiveShadow = true
                object.material.envMapItensity = 20
            }
        })
    }, [gltf])   

    // car speed

    let velocity = 0.6

    useFrame((state, delta) => {

        setTimeout(() => {
            
            // camera motion

            if(camera.current.rotation.y <= 0 && camera.current.position.x >= 20){
                camera.current.rotation.y += 0.05
                camera.current.position.z -= 0.5
            }
            if(camera.current.rotation.y == -Math.PI/2){
                camera.current.position.x += 1                
            }            
            if(camera.current.rotation.y > -Math.PI/2 && camera.current.position.z >= -280){
                camera.current.position.z -= 0.7
                setTimeout(() => {
                    if(camera.current.position.x > 10){
                        camera.current.position.x -= 0.5                     
                    }
                }, 2000);
            }
            planeMesh.current.position.z += velocity           

            if(planeMesh.current.position.z >= 330){
                velocity = 0.2
            }            

        }, 1000);

    })

    return(
        <perspectiveCamera makeDefault ref={camera} rotation={[0, -Math.PI/2, 0]} fov={50} aspect={width/height} near={1} far={1000} position={[0, -35, -30]}>

            {/* middle */}

            <mesh rotation={[Math.PI/2, 0, 0]} position={[0, -50, 50]}>
                <sphereBufferGeometry attach='geometry' args={[70, 60, 30]} />
                <meshStandardMaterial attach='material' map={earth} side={DoubleSide} />
            </mesh>  


            
            <mesh ref={planeMesh} position={[0, 15, 0]}>
                <primitive object={gltf.scene} />
            </mesh>                               

            <color args={[0, 0, 0]} attach="background" />
        </perspectiveCamera>
    )
}

const Model = () => {
    return (
        <div className='canvas'>
            <Canvas>
                <ambientLight />
                <pointLight />
                <Stars />
                <OrbitControls maxPolarAngle={1.45} target={[0, 0.35, 0]} />
                <AirZone />
            </Canvas>
        </div>
    );
};

export default Model;