import React from 'react';
import './Scss/Header.scss';
import { BrowserRouter, Link, Switch, Route } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Header = ({ english, uzbek, russian, dataPartners, dataImages }) => {

    const {t} = useTranslation()

    return (
        <div className='Header'>
            <div className='wrapper'>
                <div className="col-md-12 home-image">
                    {dataImages.map((data) => (
                        data.id == 1 &&
                        <img src={data.image} />
                    ))}
                </div>
                <div className='row'>
                    <div className="col-md-12">
                        <h1 className="title">{t("slogancha")}</h1>
                        <p>{t("slogandown")}</p>
                        <div className="d-flex">
                            <Link to="/about" className='btn red'>{t("explore")}</Link>
                            <a href='#contacts' className="btn">{t("contact")}</a>
                        </div>
                    </div>
                </div>
            </div>
            <div className='col-md-12 partners'>
                <div className="body">
                    {dataPartners.map((data) => (
                        <div className="col-md-2">
                            <img src={data.image} />
                        </div>
                    ))}                    
                </div>
            </div>            
        </div>
    );
};

export default Header;