import React, { useState } from 'react';
import './Scss/Contacts.scss';
import { Link } from 'react-router-dom';
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import { useTranslation } from 'react-i18next';

const Contacts = ({english, uzbek, russian, changeModal}) => {

    const [nameValue, setNameValue] = useState("")
    const [emailValue, setEmailValue] = useState("")
    const [value, setValue] = useState("")    

    const [redName, setRedName] = useState(false);
    const [redEmail, setRedEmail] = useState(false)
    const [redText, setRedText] = useState(false)

    function handleChange1(item){
        setNameValue(item)
        setRedName(false)
    }
    function handleChange2(item){
        setEmailValue(item)
        setRedEmail(false)
    }
    function handleChange3(item){
        setValue(item)
        setRedText(false)
    }

    let bot = {
        TOKEN: "5363076872:AAGtR7fdtEpQhEYVzVp8KFagM1tDX7a9gmQ",
        chatID: "-1001590160279",
        message: `Ism: ${nameValue}, Email: ${emailValue}, Xabar: ${value}`
    }

    function sendMessage(e){          
        if(nameValue === ""){            
            setRedName(true)
        }else if(emailValue === ""){           
            setRedEmail(true)
        }else if(value === ""){            
            setRedText(true)
        }else{
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method : "GET"
            })            
            .then(success => {
                console.log('send message')
            }, error => {
                console.log(error)
            })

            setNameValue("")
            setEmailValue("")
            setValue("")
            changeModal()
        }
        
    }

    const {t} = useTranslation()

    return (
        <>
            <div className='Contacts' id="contacts">
                <div className='wrapper'>
                    <div className='row algin-items-center justify-content-between'>
                        <div className='col-md-4 title'> 
                        <h1 className='any'>{t("quet")}</h1>
                        <p>{t("down")}</p>    
                            {/* {english &&     
                                <>
                                    <h1 className='any'>Anything to talk?</h1>
                                    <p>We would love to talk to you about any project or to provide any consultation on logistics</p>                           
                                </>                           
                            }
                            {russian &&                                
                                <>
                                    <h1 className='any'>Anything to talk?</h1>
                                    <p>We would love to talk to you about any project or to provide any consultation on logistics</p>                           
                                </>                           
                            }
                            {uzbek &&                                
                                <>
                                    <h1 className='any'>Anything to talk?</h1>
                                    <p>We would love to talk to you about any project or to provide any consultation on logistics</p>                           
                                </>                           
                            } */}
                        </div> 
                        <div className='col-md-8 message'>
                            <div className='col-5'>
                                <label className='form-label label1' htmlFor='name'>{t("ism")}</label>
                                {/* {english &&
                                    <label className='form-label label1' htmlFor='name'>Name</label>
                                }
                                {russian &&
                                    <label className='form-label label1' htmlFor='name'>Имя</label>
                                }
                                {uzbek &&
                                    <label className='form-label label1' htmlFor='name'>Ism</label>
                                }                                 */}
                                <input className={`${redName && "red-line"} form-control`} type="text" value={nameValue} onChange={(e) => handleChange1(e.target.value)} id='name' placeholder='Abdulhamid' />                                
                                <label className='form-label label2' htmlFor='number'>{t("E-mail")}</label>
                                {/* {english &&
                                    <label className='form-label label2' htmlFor='number'>E-MAIL</label>
                                }
                                {russian &&
                                    <label className='form-label label2' htmlFor='number'>Э-MAИЛ</label>
                                }
                                {uzbek &&
                                    <label className='form-label label2' htmlFor='email'>E-MAIL</label>
                                }                                 */}
                                <input className={`${redEmail && "red-line"} form-control`} type="email" value={emailValue} onChange={(e) => handleChange2(e.target.value)} id='email' placeholder='abdulhamid@abba.uz' />                                
                            </div>
                            <div className='col-7'>
                                <label className='form-label' htmlFor='question'>{t("xabar")}</label>
                                {/* {english &&
                                    <label className='form-label' htmlFor='question'>Message</label>
                                }
                                {russian &&
                                    <label className='form-label' htmlFor='question'>Message</label>
                                }
                                {uzbek &&
                                    <label className='form-label' htmlFor='question'>Xabar</label>
                                }                                 */}
                                <textarea className={`${redText && "red-line"} form-control`} rows="6" id='question' value={value} onChange={(e) => handleChange3(e.target.value)} placeholder='I want to talk about...'></textarea>
                                <div className='col-3'>
                                    <button className='btn' onClick={() => sendMessage()}>{t("send")}</button>
                                    {/* {english && 
                                        <button className='btn' onClick={() => sendMessage()}>Send</button>
                                    }
                                    {russian && 
                                        <button className='btn' onClick={() => sendMessage()}>Отправлять</button>
                                    }
                                    {uzbek && 
                                        <button className='btn' onClick={() => sendMessage()}>Yuborish</button>
                                    }                     */}
                                </div>
                            </div>
                        </div>                          
                    </div>
                </div>
            </div>
        </>
    );
};

export default Contacts;