import { Canvas, useFrame, useLoader } from '@react-three/fiber';
import React, { useEffect, useRef } from 'react';
import './Scss/Ocean.scss';
import { OrbitControls, Stars, useTexture, MeshReflectorMaterial, CubeCamera, Environment } from '@react-three/drei';
import { DoubleSide, LinearEncoding, Mesh, RepeatWrapping, TextureLoader } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

const OceanZone = () => {

    const width = window.innerWidth
    const height = window.innerHeight
    const camera = useRef(null)
    const plane = useRef(null)
    const boat = useRef(null)

    // plane

    const ground  = useTexture("images/ground1.jpg")
    const sky  = useTexture("images/sky1.png")   

    // car model gltf

    const gltf = useLoader(
        GLTFLoader,
        process.env.PUBLIC_URL + "models/car/scene.gltf"
    )

    useEffect(() => {
        gltf.scene.scale.set(0.01, 0.01, 0.01)
        gltf.scene.position.set(0, 1, 0)
        gltf.scene.traverse((object) => {
            if(object instanceof Mesh){
                object.castShadow = true
                object.receiveShadow = true
                object.material.envMapItensity = 20
            }
        })
    }, [gltf])

    // boat speed

    let velocity = 0.1



    useFrame((state, delta) => {

        setTimeout(() => {
            
            // camera motion 

            if(camera.current.position.y < -2){
                camera.current.position.y += 0.1
            }
            if(camera.current.position.z <= -3){
                camera.current.position.z += 0.1
            }
        }, 1000);

    })

    return(
        <perspectiveCamera makeDefault ref={camera} rotation={[0, -Math.PI/2, 0]} fov={50} aspect={width/height} near={1} far={1000} position={[0, -10, -30]}>
            <mesh rotation={[Math.PI/2, 0, 0]} position={[0, -3, 0]}>
                <sphereBufferGeometry attach='geometry' args={[76, 60, 30]} />
                <meshStandardMaterial attach='material' map={sky} side={DoubleSide} />
            </mesh>           
            <mesh position={[0, -0.035, 0]} ref={plane} rotation={[-Math.PI/2, 0, 0]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[-50, -0.035, 0]} ref={plane} rotation={[-Math.PI/2, 0, 0]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>   
            <mesh position={[0, -0.035, 50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[-50, -0.035, 50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>   
            <mesh position={[50, -0.035, 50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[-50, -0.035, 0]} ref={plane} rotation={[-Math.PI/2, 0, 0]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[50, -0.035, 0]} ref={plane} rotation={[-Math.PI/2, 0, 0]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[-50, -0.035, -50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[50, -0.035, -50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh position={[0, -0.035, -50]} ref={plane} rotation={[-Math.PI/2, 0, Math.PI]} castShadow receiveShadow>
                <planeGeometry args={[50, 50]} />
                <meshStandardMaterial attach='material' map={ground} />
            </mesh>
            <mesh ref={boat} position={[0, 0, 0]}>
                <primitive object={gltf.scene} />
            </mesh>           
            <color args={[0, 0, 0]} attach="background" />
        </perspectiveCamera>
    )
}

const Ocean = () => {
    return (
        <div className='canvas'>
            <Canvas>
                <ambientLight />
                <pointLight />
                <OrbitControls maxPolarAngle={1.45} target={[0, 0.35, 0]} />
                <OceanZone />
            </Canvas>
        </div>
    );
};

export default Ocean;