import React, { useEffect, useState } from 'react';
import './Scss/Numbers.scss';
import CountUp, { useCountUp } from 'react-countup';
import World from './images/world.png';
import Coordinate from './Coordinate';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

const Numbers = ({dataPartners}) => {

    const [count, setCount] = useState(300)
    const [counter, setCounter] = useState(false)

    useEffect(() => {
        function animate(){
            requestAnimationFrame(animate)
            if(window.scrollY>=3300){
                setCounter(true)
            }
        }
        animate()
    }, [])

    const {t} = useTranslation()

    return (
        <div className='Numbers'>
            {/* <Coordinate /> */}
            <div className="wrapper">
                <h1 className="title">
                    {t("numb")}
                </h1>
                <div className='row f1' id='statistics' >
                    <div className="col-md-3">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>countries</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count/2} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>wherehouses</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count/3} />+</span>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={counter && count} />+</span>
                        </div>
                    </div>
                </div>
                <div className='f2' id='statistics'>
                    <div className="col-6">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={4} end={300} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>countries</p>
                            <span className="num"><CountUp start={0} duration={3} end={150} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>wherehouses</p>
                            <span className="num"><CountUp start={0} duration={3} end={100} />+</span>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="body">
                            <p>supply engineer</p>
                            <span className="num"><CountUp start={0} duration={3} end={300} />+</span>
                        </div>
                    </div>
                </div>
                <div className="col-md-8 text">
                    <p>{t("home-numbers")}</p>
                    <div className="btns">
                        <a href='#contact' className="btn">{t("contact")}</a>                        
                    </div>
                </div>
                <div className="world col-md-6">
                    {/* <Coordinate /> */}
                    <img src={World} className="w-100" alt="" />
                </div>
                <div className="col-md-8 wh-count">
                    <h1>{t("country")}</h1>
                    <p>{t("mamlakat")}</p>
                    <div className="btns">
                        <a href='/about/#whcountry' className="btn">{t("explore")}</a>
                        {/* <button className="btn media">Mijozlarimizning raqami</button> */}
                    </div>
                </div>
                <div className="partners" id='partners'>
                    <div className="our col-md-4">
                        <h1>{t("partner")}</h1>
                    </div>
                    <div className="col-md-8 imgs">
                        {dataPartners.map((data) => (
                            <div className="col-md-4">
                                <img src={data.image} alt="" />
                            </div>
                        ))}                        
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Numbers;